"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import {
  LayoutDashboard,
  Calendar,
  Grid3X3,
  Users,
  DollarSign,
  BarChart3,
  MessageSquare,
  CheckSquare,
  CreditCard,
  Bell,
  Settings,
  Menu
} from "lucide-react"
import { Button } from "@/components/ui/button"
import { useState } from "react"

const sidebarItems = [
  { icon: LayoutDashboard, label: "Dashboard", href: "/dashboard-centros" },
  { icon: Calendar, label: "Bookings", href: "/dashboard-centros/reservas" },
  { icon: Grid3X3, label: "Courts", href: "/dashboard-centros/courts" },
  { icon: Users, label: "Customers", href: "/dashboard-centros/customers" },
  { icon: DollarSign, label: "Revenue", href: "/dashboard-centros/revenue" },
  { icon: BarChart3, label: "Analytics", href: "/dashboard-centros/analytics" },
  { icon: MessageSquare, label: "Messages", href: "/dashboard-centros/messages" },
  { icon: CheckSquare, label: "Tasks", href: "/dashboard-centros/tasks" },
  { icon: CreditCard, label: "Billing", href: "/dashboard-centros/billing" },
  { icon: Bell, label: "Notifications", href: "/dashboard-centros/notifications" },
  { icon: Settings, label: "Settings", href: "/dashboard-centros/settings" },
]

export function AppSidebar() {
  const pathname = usePathname()

  return (
    <div className="flex flex-col h-screen w-64 border-r bg-card/50 hidden md:flex sticky top-0">
      <div className="p-6">
        <h1 className="text-xl font-bold tracking-tight flex items-center gap-2">
          <div className="size-8 rounded-lg bg-primary/10 flex items-center justify-center">
             <Grid3X3 className="size-5 text-primary" />
          </div>
          ClubManager
        </h1>
      </div>
      
      <div className="flex-1 overflow-y-auto py-2 px-3 space-y-1">
        {sidebarItems.map((item) => {
          const isActive = pathname === item.href
          return (
            <Link
              key={item.href}
              href={item.href}
              className={cn(
                "flex items-center gap-3 px-3 py-2 text-sm font-medium rounded-md transition-colors",
                isActive 
                  ? "bg-primary/10 text-primary" 
                  : "text-muted-foreground hover:bg-muted hover:text-foreground"
              )}
            >
              <item.icon className="size-4" />
              {item.label}
            </Link>
          )
        })}
      </div>

      <div className="p-4 border-t">
         <div className="flex items-center gap-3">
            <div className="size-8 rounded-full bg-muted flex items-center justify-center overflow-hidden">
                {/* Avatar placeholder */}
                <div className="w-full h-full bg-gradient-to-br from-blue-500 to-purple-500" />
            </div>
            <div className="text-sm">
                <p className="font-medium">Club Admin</p>
                <p className="text-xs text-muted-foreground">admin@club.com</p>
            </div>
         </div>
      </div>
    </div>
  )
}

export function MobileSidebar() {
    const pathname = usePathname()
    const [open, setOpen] = useState(false)

    return (
        <div className="md:hidden flex items-center justify-between p-4 border-b bg-background sticky top-0 z-50">
             <div className="flex items-center gap-2 font-bold text-lg">
                <Grid3X3 className="size-5 text-primary" />
                ClubManager
             </div>
            {/* Simple Sheet implementation or just a trigger for now if Sheet is not available, 
                checking UI library again... Sheet wasn't there. 
                I'll handle mobile nav simply for now to avoid errors. 
            */}
             <Button variant="ghost" size="icon" onClick={() => setOpen(!open)}>
                 <Menu className="size-5" />
             </Button>
             
             {open && (
                 <div className="absolute top-16 left-0 right-0 bg-background border-b z-50 shadow-lg animate-in slide-in-from-top-5">
                     <nav className="flex flex-col p-4 space-y-2">
                        {sidebarItems.map((item) => (
                             <Link
                                key={item.href}
                                href={item.href}
                                onClick={() => setOpen(false)}
                                className={cn(
                                    "flex items-center gap-3 px-3 py-2 text-sm font-medium rounded-md",
                                    pathname === item.href
                                    ? "bg-primary/10 text-primary"
                                    : "text-muted-foreground hover:bg-muted"
                                )}
                             >
                                <item.icon className="size-4" />
                                {item.label}
                             </Link>
                        ))}
                     </nav>
                 </div>
             )}
        </div>
    )
}
